.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgb(167, 209, 243);
  height: 100vh;
  width: 100vw;
}

.wrapper {
  padding: 35px;
  border: .1px solid rgb(221, 153, 136);
  display: flex;
  flex-direction: column;
  width: 40%;
  height: auto;
  background: rgba(219,218,218, 0.25);
  box-shadow: 0 8px 32px 0 rgb(0, 47, 92);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(4px);
  border-radius: 10px;
  border: 1px solid rgba(255, 253, 253, 0.25);

}

.app_img {
  margin: 24px;
  max-height: 500px;
  width: auto;
  border-radius: 5px;
}
